      hsl(45, 75%, 75%)
    );
  background: black
    radial-gradient(
      circle farthest-side at calc(var(--mouseX, 1) * 1px)
        calc(var(--mouseY, 1) * 1px),
      hsl(var(--mouseX, 5), 75%, 75%),
      hsl(var(--mouseY, 45), 75%, 75%)
    );
}

