.NotFound {
  background: #000;
  height: 100vh;
}


.NotFound html {
  margin: 0 auto;
  padding: 0;
  font-weight: 800;
}
.NotFound body {
  background: #000;
  font-family: cursive;
}
.NotFound svg {
  display: block;
  font: 10.5em 'Monoton';
  width: 960px;
  height: 300px;
  margin: 0 auto;
}
.NotFound .content {
  text-align: center;
}
.NotFound h1 {
  text-align: center;
  font: 2em 'Roboto', sans-serif;
  font-weight: 900;
  color: #2f8f7f;
  opacity: 0.6;
}
.NotFound a {
  display: inline-block;
  text-transform: uppercase;
  font: 1em 'Roboto';
  font-weight: 300;
  border: 1px solid #2f8f7f;
  border-radius: 4px;
  color: #2f8f7f;
  margin-top: 10%;
  padding: 8px 14px;
  text-decoration: none;
  opacity: 0.6;
}
.NotFound .text {
  fill: none;
  stroke: white;
  stroke-dasharray: 8% 29%;
  stroke-width: 5px;
  stroke-dashoffset: 1%;
  -webkit-animation: stroke-offset 5.5s infinite linear;
          animation: stroke-offset 5.5s infinite linear;
}
.NotFound .text:nth-child(1) {
  stroke: #1c234d;
  -webkit-animation-delay: -1;
          animation-delay: -1;
}
.NotFound .text:nth-child(2) {
  stroke: #315b2c;
  -webkit-animation-delay: -2s;
          animation-delay: -2s;
}
.NotFound .text:nth-child(3) {
  stroke: #2f8f7f;
  -webkit-animation-delay: -3s;
          animation-delay: -3s;
}
.NotFound .text:nth-child(4) {
  stroke: #2f8f7f;
  -webkit-animation-delay: -4s;
          animation-delay: -4s;
}
.NotFound .text:nth-child(5) {
  stroke: #da2717;
  -webkit-animation-delay: -5s;
          animation-delay: -5s;
}
@-webkit-keyframes stroke-offset  {
  100% {
    stroke-dashoffset: -35%;
  }
}
@keyframes stroke-offset  {
  100% {
    stroke-dashoffset: -35%;
  }
}



